@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

@font-face {
  font-family: 'Echelon';
  src: url('./assets/fonts/Echelon.ttf'), format(truetype);
  src: url('./assets/fonts/Echelon.woff'), format(woff);
  src: url('./assets/fonts/Echelon.woff2'), format(woff2);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
  outline: none;
  box-sizing: border-box;
  text-decoration: none;
  list-style: none;
}

:root {
  --primary_color: #cfbd97;
  --second_color: #000000;
  --black: #1e1e1e;
  --white: #fff;
  --white2: #f5f5f5;
  --main_font: "Echelon";
  --trans: .3s ease-in;
}

body {
  background-color: var(--second_color);
  width: 100%;
  overflow-x: hidden;
}